.group {
    display: flex;
    width: 580px;
    /* height: 100%; */
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0 ;
    margin: 0 16px;
    gap: 24px;
    /* border: 1px solid; */
  }

  .item {
    position: relative;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 272px;
    height: 208px;
    list-style: none;
  }
/* 
  .item:hover {
    border: 1px solid yellow;
  } */
  
.price {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

h4 {
    /* font-family: 'JetBrains Mono'; */
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
}

.price svg {
    margin-left: 5px;
}